import React from 'react';
import { Table, TableRow, TableHead, TableBody, Paper, TableCell, Typography } from '@material-ui/core';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
        overflowX: 'auto',
    },
    table: {
        width: "100%",
        minWidth: 0,
        colWidth: '2rem',
        tableLayout: 'fixed',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    tideLevelHigh: {
        backgroundColor: 'lightBlue',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid',
        textColor: 'yellow'
    },
    tideLevelLow: {
        backgroundColor: 'lightGray',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    columnHeader: {
        backgroundColor: 'darkGray',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    }
});


const TideTimesGrid = (props) => {

    const { classes } = props;

    const headers = [];

    if (!props['data']) {
        return (<Typography variant="h6">No Data for Site</Typography>)
    }
    Object.keys(props['data']).forEach((date, index) => {
        headers.push(date.slice(6) + "/" + date.slice(4, 6) + "/" + date.slice(2, 4));
    });

    const transformedData = [[], [], [], []];
    for (let x in [0, 1, 2, 3]) {
        Object.keys(props['data']).forEach((date, index) => {
            const timesData = props['data'][date];
            if (timesData) {
                transformedData[x].push(timesData[x]);
            } else {
                transformedData[x].push({});
            }
        });
    }

    return (

        <Paper className={classes.root}>
            <Table className={classes.table}>
                <TableHead className={classes.columnHeader}>
                    <TableRow>
                        { // Header
                            headers.map((header, index) => {
                                return (<TableCell key={index}><Typography variant="h6">{header}</Typography></TableCell>);
                            })
                        }
                    </TableRow>
                </TableHead>
                <TableBody>
                    { // Each row
                        transformedData.map((data, indexOuter) => {
                            return (
                                <TableRow key={indexOuter}>
                                    {
                                        data.map((timeData, indexInner) => {
                                            if (timeData && timeData.time) {
                                                return (
                                                    <TableCell
                                                        className={timeData.isLow ? classes.tideLevelLow : classes.tideLevelHigh}
                                                        key={indexOuter + indexInner}
                                                        color="primary"
                                                    >
                                                        <Typography variant="h6" >{timeData.time.trim()}</Typography>
                                                    </TableCell>
                                                );
                                            } else {
                                                return (
                                                    <TableCell className={classes.tideLevelLow} key={indexOuter + indexInner}>
                                                        <Typography variant="body1">{"----".trim()}</Typography>
                                                    </TableCell>
                                                )
                                            }
                                        })
                                    }
                                </TableRow>
                            );
                        })
                    }
                </TableBody>
            </Table>
        </Paper>

        /*
        return (
            <div className={classes.root}>
                <Grid item className={classes.root} xs={12}>

                    <Grid item>
                        <Typography variant="h4">{this.ucfirst(siteId)}</Typography>
                    </Grid>

                    <Grid item>
                        <Typography variant="h6">{date}</Typography>
                    </Grid>
                </Grid>

                {
                    times.map((time, i) => {
                        return (
                            <Grid item key={i} xs={12}>
                                <Grid>
                                    <Grid item xs={4} className={classNames(time.isLow ? classes.tideLevelLow : classes.tideLevelHigh)}>
                                        {time.time}
                                    </Grid>
                                </Grid>
                            </Grid>
                        )
                    })
                }
            </div>
        );
        */
    )

}

TideTimesGrid.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TideTimesGrid);
